<template>
  <div class="listViewTop">
    <img
      :src="playlist.coverImgUrl"
      class="bg"
      alt=""
    >
    <div class="listViewTopNav">
      <div
        class="back"
        @click="$router.go(-1)"
      >
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-arrow-prev"></use>
        </svg>
        <span class="title">歌单</span>
      </div>
      <div class="right">
        <svg
          class="icon search"
          aria-hidden="true"
        >
          <use xlink:href="#icon-sousuo"></use>
        </svg>
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-liebiao"></use>
        </svg>
      </div>
    </div>
    <div class="content">
      <div class="contentLeft">
        <img
          :src="playlist.coverImgUrl"
          alt=""
        >
        <div class="count">
          <svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-bofang-"></use>
          </svg>
          <span>
            {{changeValue(playlist.playCount)}}
          </span>
        </div>
      </div>
      <div class="contentRight">
        <h3>{{playlist.name}}</h3>
        <div class="author">
          <img
            :src="playlist.creator.avatarUrl"
            alt=""
            class="header"
          >
          <span>{{playlist.creator.nickname}}</span>
        </div>
        <div class="description">
          {{playlist.description}}
        </div>
      </div>
    </div>
    <div class="iconList">
      <div class="iconItem">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-liaotian"></use>
        </svg>
        <span>{{playlist.commentCount}}</span>
      </div>
      <div class="iconItem">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-fenxiang"></use>
        </svg>
        <span>{{playlist.shareCount}}</span>
      </div>
      <div class="iconItem">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-download"></use>
        </svg>
        <span>下载</span>
      </div>
      <div class="iconItem">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-iconfontxuanzekuangyixuan"></use>
        </svg>
        <span>多选</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props:['playlist'],
    setup() {
      function changeValue(num){
       let res = 0;
       if(num>=100000000){
         res = num/100000000;
         res = res.toFixed(2) + '亿';
       }else if(num>=10000){
         res = num/10000;
         res = res.toFixed(2) + '万';
       }
       return res;
     }
     return {
       changeValue,
     }
    }
  }
</script>

<style lang="less" scoped>
.listViewTop {
  width: 7.5rem;
  padding: 0 0.4rem;
  .bg {
    position: fixed;
    left: 0;
    top: 0;
    width: 7.5rem;
    height: auto;
    z-index: -1;
    filter: blur(40px);
  }
  .listViewTopNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.2rem;
    font-size: 0.3rem;
    .icon {
      width: 0.5rem;
      height: 0.5rem;
      fill: #fff;
    }
    .title {
      margin-left: 0.4rem;
    }
    .search {
      margin-right: 0.5rem;
    }
    .back,
    .right {
      display: flex;
      color: #fff;
      align-items: center;
    }
  }
  .content {
    display: flex;
    justify-content: space-between;
    margin: 0.6rem 0;
    .contentLeft {
      position: relative;
      img {
        width: 2.8rem;
        height: 2.8rem;
        border-radius: 0.1rem;
      }
      .count {
        position: absolute;
        right: 0.1rem;
        top: 0.1rem;
        color: #aaa;
        font-size: 0.24rem;
        display: flex;
        align-items: center;
        font-weight: 900;
        .icon {
          fill: #ccc;
        }
      }
    }
    .contentRight {
      width: 3.5rem;
      h3 {
        color: #fff;
      }
      .author {
        margin: 0.2rem 0;
        display: flex;
        align-items: center;
        img.header {
          width: 0.6rem;
          height: 0.6rem;
          border-radius: 0.3rem;
          margin-right: 0.2rem;
        }
        span {
          color: #ccc;
          font-size: 0.26rem;
        }
      }
      .description {
        font-size: 0.24rem;
        color: #ccc;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }
  }
  .iconList {
    display: flex;
    justify-content: space-around;
    .iconItem {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .icon {
        fill: #fff;
        width: 0.5rem;
        height: 0.5rem;
      }
      span {
        font-size: 0.14rem;
        padding-top: 0.1rem;
        color: #fff;
      }
    }
  }
}
</style>